import React from "react"
import  headshot  from '../images/headshot.gif';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import About from './About';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Projects from './Projects';
import OtherInterests from './OtherInterests';
import Contact from './Contact';
import { Router} from "@reach/router";
import '../index.css';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const theme = createMuiTheme({
  typography: {
    fontFamily: [
      '"Unica One"',
      '"Raleway"',
    ].join(','),
  }
})

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    margin: '100px',
    marginLeft: '10%',
    marginRight: '10%',
    background: 'rgba(247, 247, 247, 0.21)',
  },
  text: {
    fontFamily: 'Raleway',
  }
});

const Home = (props) => {

  const { classes } = props;

  return (
    <MuiThemeProvider theme={theme}>

    <div className="index">
    <Header/>
      <Paper
          className={classes.root}
          elevation={2}>
          <Typography className="home-heading"  variant="h4" align="center" >
            Full-Stack Software Engineer
          </Typography>
          <img className="index-img" src={headshot} alt="Me" />
          <Router>
            <About path="/About"/>
            <Projects path='/Projects'/>
            <OtherInterests path='/OtherInterests'/>
            <Contact path='/Contact'/>
          </Router>


        </Paper>
        <Footer/>

  </div>

        </MuiThemeProvider>





  )
}

Home.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Home);

